@font-face {
  font-family: T-bold;
  src: url(/src/fonts/TitilliumWeb-Bold.ttf);
}
@font-face {
  font-family: T-regular;
  src: url(/src/fonts/TitilliumWeb-Regular.ttf);
}
.reactIcon {
  animation-name: IconRotate;
  animation-duration: 4s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes IconRotate {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.headPart {
  display: flex;
  width: 80%;
  flex-direction: raw;
  flex-wrap: nowrap;
}
.headPart__img {
  width: 70px;
  height: 70px;
  margin-top: 5px;
}
.headPart__header {
  margin-left: 20px;
  font-family: T-bold, sans-serif;
  font-size: 3.5rem;
}

.input {
  padding: 30px 30px 30px 30px;
  width: 90%;
  height: 200px;
  margin-top: 30px;
  border-radius: 30px;
  background: #1c1c1c;
  box-shadow: 20px 20px 40px #0f0f0f;
  display: flex;
  flex-direction: raw;
  flex-wrap: nowrap;
  animation-name: appearInput;
  animation-duration: 0.5s;
}
.input__inputs {
  width: 100%;
  background-color: #1c1c1c;
}
.input__inputs__name {
  width: 50%;
  background-color: #1c1c1c;
  border-bottom: solid white 2px;
  font-family: T-bold, sans-serif;
  font-size: 30px;
  transition-duration: 1s;
  padding-left: 10px;
  opacity: 0.5;
}
.input__inputs__name:focus {
  opacity: 1;
  width: 90%;
}
.input__inputs__task {
  width: 90%;
  height: 30px;
  text-align: left;
  margin-top: 20px;
  background-color: #1c1c1c;
  border-bottom: solid white 0.1px;
  padding-bottom: 5px;
  font-size: 20px;
  transition-duration: 1s;
  padding-left: 10px;
  opacity: 0.5;
  resize: none;
}
.input__inputs__task:focus {
  opacity: 1;
  height: 60%;
}
.input__create {
  margin-top: auto;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  background-color: white;
  color: lime;
  font-family: T-bold, sans-serif;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition-duration: 0.3s;
}
.input__create__img {
  width: 70%;
  height: 70%;
  background-color: white;
}
.input__create:hover {
  cursor: pointer;
  width: 50px;
  height: 50px;
}
.input__buttonContent {
  width: 60px;
  height: 60px;
  margin-top: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #1c1c1c;
}

@keyframes appearInput {
  from {
    width: 1%;
    opacity: 0;
  }
  to {
    width: 90%;
    opacity: 1;
  }
}
.listElement {
  padding: 30px 30px 30px 30px;
  width: 90%;
  height: 200px;
  margin-top: 30px;
  border-radius: 30px;
  background: #1c1c1c;
  box-shadow: 20px 20px 40px #0f0f0f;
  display: flex;
  flex-direction: raw;
  flex-wrap: nowrap;
  animation-name: appearEl;
  animation-duration: 0.5s;
}
.listElement__elements {
  width: 100%;
  background-color: #1c1c1c;
}
.listElement__elements__name {
  width: 90%;
  background-color: #1c1c1c;
  border-bottom: solid white 2px;
  font-family: T-bold, sans-serif;
  font-size: 30px;
  transition-duration: 1s;
  padding-left: 10px;
}
.listElement__elements__task {
  width: 90%;
  height: 60px;
  text-align: left;
  margin-top: 20px;
  background-color: #1c1c1c;
  border-bottom: solid white 0.1px;
  padding-bottom: 5px;
  font-size: 20px;
  transition-duration: 1s;
  padding-left: 10px;
}

@keyframes appearEl {
  from {
    opacity: 0;
    height: 1px;
  }
  to {
    opacity: 1;
    height: 200px;
  }
}
* {
  padding: 0;
  margin: 0;
  background-color: black;
  border: none;
  color: white;
  font-family: T-regular, sans-serif;
  overflow-x: hidden;
  outline: none;
}

button {
  background-color: #1c1c1c;
}

body {
  width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

main {
  vertical-align: middle;
  width: 800px;
  margin-top: 50px;
}

.line {
  width: 100%;
  height: 2px;
  background-color: white;
}

.todos {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 40px 40px 40px 40px;
}

::-webkit-scrollbar {
  width: 0;
  height: 0;
}

::-webkit-scrollbar-button {
  width: 0;
  height: 0;
}

.ClearBtnContent {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 40px;
  margin-bottom: 20px;
  height: 50px;
  align-items: center;
}

.ClearBtn {
  width: 50%;
  height: 40px;
  background-color: white;
  border-radius: 5px;
  color: black;
  font-family: T-bold, sans-serif;
  font-size: 25px;
  transition-duration: 0.3s;
}
.ClearBtn:hover {
  height: 50px;
  width: 55%;
  font-size: 30px;
  cursor: pointer;
}

.DeleteButtonContent {
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  margin-top: auto;
  justify-content: center;
  background-color: #1c1c1c;
}

.DeleteButton {
  width: 40px;
  height: 40px;
  transition-duration: 0.3s;
  background-color: #1c1c1c;
}
.DeleteButton:hover {
  width: 50px;
  height: 50px;
  cursor: pointer;
}

@media screen and (min-width: 767px) and (max-width: 1023px) {
  main {
    vertical-align: middle;
    width: 90%;
    margin-top: 50px;
  }
}
@media screen and (max-width: 767px) {
  main {
    vertical-align: middle;
    width: 90%;
    margin-top: 50px;
  }
}
@media screen and (max-width: 500px) {
  main {
    vertical-align: middle;
    width: 95%;
    margin-top: 10px;
  }
  .headPart__img {
    width: 50px;
    height: 50px;
    margin-bottom: 15px;
  }
  .headPart__header {
    font-size: 2rem;
    margin-top: 7px;
  }
  .input__inputs__name {
    font-size: 25px;
  }
  .input__inputs__task {
    font-size: 15px;
  }
  .input__create {
    width: 30px;
    height: 30px;
  }
  .input__create:hover {
    width: 40px;
    height: 40px;
  }
  .input__buttonContent {
    width: 50px;
    height: 50px;
  }
}